import { Injectable } from '@angular/core';
import { Response } from '@angular/http';

import { BehaviorSubject, Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { ICountryConfig } from '@cemex-core/types-v2';

import { HttpCemex } from './http.service';
import { ProjectSettings } from './project.settings';
import { SessionService } from './session.service';

@Injectable()
export class CountryConfigService {
  private _countryConfig = new BehaviorSubject<ICountryConfig>(null);
  private _countryConfigApiPath = '/v5/secm/console/countryconfig';

  constructor(
    private http: HttpCemex,
    private projectEnv: ProjectSettings,
    private sessionService: SessionService
  ) {
    if (!this._countryConfig.getValue()) {
      this.getCountryConfiguration().subscribe((data: ICountryConfig) => {
        this._countryConfig.next(data);
      });
    }
  }

  public get countryConfig(): Observable<ICountryConfig> {
    return this._countryConfig.asObservable();
  }

  public getCountryConfiguration(): Observable<ICountryConfig> {
    const userCountry = this.sessionService.userCountry || 'US';
    const url = `${this.projectEnv.getBaseOrgEnvPath()}${
      this._countryConfigApiPath
    }?countryCode=${userCountry}`;
    return this.http.get(url).pipe(
      map((res: Response) => res.json()),
      catchError(this.handleError)
    );
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body: any = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(error);
    return Observable.throw(error);
  }
}
