import { CountlyService } from './countly.service';
import {
  IAnalyticsConfiguration,
  IAnalyticsService
} from './ianalytics.interface';

export class AnalyticsFactory {
  // tslint:disable-next-line:variable-name
  public static Type = { Countly: 1, Clevertap: 2, Other: 0 };
  public createService(
    analitycServiceType,
    configuration: IAnalyticsConfiguration
  ): IAnalyticsService {
    switch (analitycServiceType) {
      case AnalyticsFactory.Type.Countly:
        const service = new CountlyService();
        service.startService(configuration.url, configuration.key);
        return service;
      case AnalyticsFactory.Type.Clevertap:
        return null;
      case AnalyticsFactory.Type.Other:
        return null;
      default:
        return null;
    }
  }
}
